.c-btn {
  outline: none;
  cursor: pointer;
	overflow: hidden;
	position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: auto;
  height: 57px;
	padding-left: 30px;
	padding-right: 30px;
  border: 0 solid #b1e4e8;
  box-shadow: none;
  border-radius: 0;
	background-color: #b1e4e8;
	text-align: center;
	@include transition(background-color);
	@include adaptive(1279) {
		height: 50px;
	}
	@include adaptive(1023) {
		padding-left: 15px;
		padding-right: 15px;
	}

  &__wrapper {
		display: flex;

		&--center {
			justify-content: center;
		}
	}

	&:hover {
		background-color: #000000;
		@include transition(background-color);

		span {
			color: #ffffff;
			@include transition(color);
		}
		//&:before,
		//&:after {
		//	@include transition(transform);
		//}
		//
		//&:before {
		//	transform: scaleX(0);
		//}
		//&:after {
		//	transform: scaleY(0);
		//}
	}

	//&:before,
	//&:after {
	//	content: '';
	//	position: absolute;
	//	top: 0;
	//	left: 0;
	//	width: 100%;
	//	height: 100%;
	//	background-color: #b1e4e8;
	//	@include transition(transform);
	//}
	//
	//&:before {
	//	transform: scaleX(1);
	//}
	//&:after {
	//	transform: scaleY(1);
	//}

	span {
		z-index: 2;
		position: relative;
		color: #181819;
		font-size: 16px;
		text-transform: uppercase;
		letter-spacing: 0.5px;
		@include transition(color);
		@include adaptive(1279) {
			font-size: 13px;
		}
	}

	&--border {
		background-color: transparent;
		border: 1px solid #181819;

		&:hover {
			&:before {
				transform: scaleX(1);
			}
			&:after {
				transform: scaleY(1);
			}

			span {
				color: #ffffff;
				@include transition(color);
			}
		}

		&:before,
		&:after {
			background-color: #181819;
		}

		&:before {
			transform: scaleX(0);
		}
		&:after {
			transform: scaleY(0);
		}
	}
}

